body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 20px;
    background-color: #f5f5f5;
}

.container {
    max-width: 600px;
    margin: 0 auto;
    background: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.container .topbar {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.container .logo {
    display: flex;
    width: 100%;
    justify-content: space-a;
}

.container .logo a {
    width: 100%;
    display: flex;
    justify-content: center;
}

.container .logo img {
    width: 50%;
}

.leaflet-container {
    width: 100%;
    height: 400px;
  }

.question{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.questiontitle{
    padding-right: 10px;
}

.questiontitle input, .questiontitle select{
    margin-left: 10px;
    width: 100px;
}